/* eslint-disable react-hooks/exhaustive-deps */
import { Form, Spin, Row, Col, PageHeader, Typography, Divider } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import greetingTime from 'greeting-time';

import React, { useEffect, useState } from 'react';
import service from '../../../services/user';
import { AntdMsg } from '../../../utils/Antd';
import UploadImage from '../../../utils/UploadImage';
let count = -1;

export default function Profile() {

    const [data, setData] = useState({});
    const [fileList, setFileList] = useState([]);
    const [loading, setLoading] = useState(false);
    const [doSave, setDoSave] = useState(0);

    const handleChange = (v, k) => { setData({ ...data, [k]: v }); setDoSave(doSave + 1); }

    function list() {
        setLoading(true);
        service.profile().then(res => {
            setData(res.data.result || {});
            setFileList([{uid: res.data.result?.fileId, url: res.data.result?.profileUrl}]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setLoading(false);
        });
    }
    
    useEffect(() => {
        list();
    }, []);
    
    useEffect(()=>{
        if(count>0){
            // handleChange(fileList?.[0]?.uid, 'fileId');
        }
        count++;
    }, [fileList])

    useEffect(() => {
        if (doSave) {
            setLoading(true);
            service.saveProfile(data).then(res => {
                AntdMsg(res.message);
            }).catch(err => {
                AntdMsg(err.message, 'error');
            }).finally(() => {
                setLoading(false);
            });
        }
    }, [doSave]);

    return (
        <>
            <Spin spinning={loading} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                <PageHeader title={greetingTime(new Date()) + ', ' + data?.name} />
                <Divider />
                <Form layout="vertical">
                    <Row gutter={[12, 2]}>
                        <Col span={16}>
                            <Row gutter={[12, 10]}>
                                <Col span={12}>
                                    <Form.Item label={<Typography.Text strong type="secondary">Name</Typography.Text>}>
                                        <Typography.Title editable={{ onChange: (v) => { handleChange(v, 'name'); } }} level={5} >
                                            {data.name}
                                        </Typography.Title>
                                        <Divider style={{ margin: 0 }} />
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item label={<Typography.Text strong type="secondary">User Name</Typography.Text>}>
                                        <Typography.Title editable={{ onChange: (v) => { handleChange(v, 'userName'); } }} level={5} >
                                            {data.userName}
                                        </Typography.Title>
                                        <Divider style={{ margin: 0 }} />
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item label={<Typography.Text strong type="secondary">Email</Typography.Text>}>
                                        <Typography.Title editable={{ onChange: (v) => { handleChange(v, 'email'); } }} level={5} >
                                            {data.email}
                                        </Typography.Title>
                                        <Divider style={{ margin: 0 }} />
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item label={<Typography.Text strong type="secondary">Mobile Number</Typography.Text>}>
                                        <Typography.Title editable={{ onChange: (v) => { handleChange(v, 'mobile'); } }} level={5} >
                                            {data.mobile}
                                        </Typography.Title>
                                        <Divider style={{ margin: 0 }} />
                                    </Form.Item>
                                </Col>
                                <Col span={24}>
                                    <Form.Item label={<Typography.Text strong type="secondary">About</Typography.Text>}>
                                        <Typography.Title editable={{ onChange: (v) => { handleChange(v, 'about'); } }} level={5} >
                                            {data.about}
                                        </Typography.Title>
                                        <Divider style={{ margin: 0 }} />
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Col>
                        <Col span={8}>
                            <UploadImage {...{fileList, setFileList}} />
                        </Col>
                    </Row>
                </Form>
            </Spin>
        </>
    );
};