import axios from "../utils/axios";

export default class service {

    static baseURL = 'ecomm';
    // category : 
    static detailCategory(id, data) {
        return axios.get(this.baseURL + '/category/details/' + id, { params: data });
    }
    static listCategory(data) {
        return axios.get(this.baseURL + '/category/list', { params: data });
    }
    static saveCategory(data, query) {
        return axios.post(this.baseURL + "/category/save", data, { params: query });
    }
    static deleteCategory(id) {
        return axios.post(this.baseURL + '/category/delete', { ids: id });
    }

    // for sub category
    static detailSubCategory(id, data) {
        return axios.get(this.baseURL + '/subcategory/details/' + id, { params: data });
    }
    static listSubCategory(data) {
        return axios.get(this.baseURL + '/subcategory/list', { params: data });
    }
    static saveSubCategory(data, query) {
        return axios.post(this.baseURL + "/subcategory/save", data, { params: query });
    }
    static deleteSubCategory(id) {
        return axios.post(this.baseURL + '/subcategory/delete', { ids: id });
    }

    // for attribute
    static detailAttribute(id, data) {
        return axios.get(this.baseURL + '/attribute/details/' + id, { params: data });
    }
    static listAttribute(data) {
        return axios.get(this.baseURL + '/attribute/list', { params: data });
    }
    static saveAttribute(data, query) {
        return axios.post(this.baseURL + "/attribute/save", data, { params: query });
    }
    static deleteAttribute(id) {
        return axios.post(this.baseURL + '/attribute/delete', { ids: id });
    }


}