import axios from "../utils/axios";

export default class service {

    static baseURL = 'job';

    static detailCategory(id, data) {
        return axios.get(this.baseURL + '/category/details/' + id, { params: data });
    }
    static listCategory(data) {
        return axios.get(this.baseURL + '/category/list', { params: data });
    }
    static saveCategory(data, query) {
        return axios.post(this.baseURL + "/category/save", data, { params: query });
    }
    static deleteCategory(id) {
        return axios.post(this.baseURL + '/category/delete', { ids: id });
    }

    static detailProfile(id, data) {
        return axios.get(this.baseURL + '/profile/details/' + id, { params: data });
    }
    static listProfile(data) {
        return axios.get(this.baseURL + '/profile/list', { params: data });
    }
    static saveProfile(data, query) {
        return axios.post(this.baseURL + "/profile/save", data, { params: query });
    }
    static deleteProfile(id) {
        return axios.post(this.baseURL + '/profile/delete', { ids: id });
    }

    static detailJob(id, data) {
        return axios.get(this.baseURL + '/details/' + id, { params: data });
    }
    static listJob(data) {
        return axios.get(this.baseURL + '/list', { params: data });
    }
    static saveJob(data, query) {
        return axios.post(this.baseURL + "/save", data, { params: query });
    }
    static deleteJob(id) {
        return axios.post(this.baseURL + '/delete', { ids: id });
    }


}