import { Avatar, Layout, Menu } from "antd";
import { UserOutlined, LogoutOutlined } from '@ant-design/icons';




export default function Layout11() {

    const items = [
        {
            label: 'Sanjay Yadav',
            icon: <Avatar shape="circle" size={32} icon={<UserOutlined />} />,
            key: 'profile',
        },
        {
            label: 'Logout',
            icon: <LogoutOutlined />,
            key: 'logout',
            style: { color: 'red' },
            onClick: () => { window.localStorage.clear(); window.location.reload(); }
        },
    ];

    return (
        <>
            <Layout.Header style={{ padding: 0, height: 'auto' }} >
                <Menu mode="horizontal" items={items} style={{ lineHeight: '46px' }} />
            </Layout.Header>
        </>
    );

}